(function () {
    'use strict';
    var angular = require('angular');

    var nonSourcingCity = angular.module('app').component('officeNonSourcingCity', {
        template: require('./office-non-sourcing-city.component.html'),
        controller: ['analyticService', 'appState', function (analyticService, appState) {
            var vm = this;
            vm.$onInit = function () {
                analyticService.trackPageLoad(analyticService.pageList.officeNonSourcingCity);
                appState.clearAll();
            };
            vm.exit = function () {
                analyticService.buttonList.buttonClick.event_content = "exit";
                analyticService.trackUserAction(analyticService.buttonList.buttonClick);
                window.location.href = "http://www.hsbc.co.in/"
            }
        }]
    });

    module.exports = nonSourcingCity.name;
})();



